import { FunctionalComponent, h } from '@stencil/core';

export type FcInputFeedbackType = {
  id: string;
  type: string;
  text: string;
};

const feedBackIcons = {
  none: null,
  error: { icon: 'error_solid', color: 'error' },
  success: { icon: 'check_2_solid', color: 'success' },
  warning: { icon: 'exclamation_solid', color: 'warning' },
};

export const FcInputFeedback: FunctionalComponent<FcInputFeedbackType> = props => {
  const iconName = feedBackIcons[props.type].icon;
  const iconColor = feedBackIcons[props.type].color;

  const getFeedbackClass = () => ({
    'ath-input__feedback': true,
    [`ath-input__feedback--${props.type}`]: !!props.type,
  });

  return (
    !!props.text && (
      <div class={getFeedbackClass()} aria-live="polite" id={props.id}>
        <ath-icon icon={iconName} color={iconColor} size="sm"></ath-icon>
        <span class="padding">{props.text}</span>
      </div>
    )
  );
};
